import React from 'react';

const Popover = ({ isOpen, onClose, children }) => {
  if (!isOpen) return null;

  return (
    <div
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
      onClick={onClose} // Close the popover when clicking outside
    >
      <div
        className="relative bg-white p-6 rounded-lg shadow-lg max-w-3xl w-full" // max-w-3xl or you can adjust it
        onClick={(e) => e.stopPropagation()} // Prevent closing when clicking inside the popover
      >
        <button
          className="absolute top-2 right-2 text-gray-500 hover:text-gray-700"
          onClick={onClose} // Close the popover when clicking the close button
        >
          âœ•
        </button>
        {children} {/* Render passed children like ShowProducts */}
      </div>
    </div>
  );
};

export default Popover;
